import React, { Component } from 'react';
import GoogleMap from './GoogleMap';
import { navigate } from '@reach/router';


class ShowLocation extends Component {



  constructor() {
    super();
    this.state = {
    };
    
    this.mapsSelector = this.mapsSelector.bind(this);
    this.navigate = this.navigate.bind(this);

  }


  componentDidMount(){
    console.log("hello from show location;");
  }

  mapsSelector(e) {
    console.log('its clicking');
    e.preventDefault();
    if /* if we're on iOS, open in Apple Maps */
      ((navigator.platform.indexOf("iPhone") !== -1) || 
       (navigator.platform.indexOf("iPad") !== -1) || 
       (navigator.platform.indexOf("iPod") !== -1))
      window.open(`maps://maps.google.com/maps?daddr=${this.props.odLat},${this.props.odLng}&amp;ll=`);
  else /* else use Google */
      window.open(`https://maps.google.com/maps?daddr=${this.props.odLat},${this.props.odLng}&amp;ll=`);
  }


  navigate(event){
    event.preventDefault();
    var buttonID = event.target.id;
    navigate(`/${buttonID}`);
}

  
  render() {

    const { lat, lng, odLat, odLng } = this.props;


    
    return (



<div className='wrapper'>

    <div className='container h-100 d-flex flex-column align-items-center m-0 p-0 bg-warning'>
         
        <div className="d-flex bg-primary h-50 w-100 p-0 m-0">
      
        
              {lat && (
              <GoogleMap lat={lat} lng={lng}  odLat={odLat} odLng={odLng} /> 
              )}
           
        </div>
        <div className="d-flex bg-light h-50 w-100 p-4 border-top">
            <div className="align-self-center">
                <h4 className="text-center">The yellow marker is the overdose victim's location</h4>
                <button className="btn btn-warning font-weight-bold text-uppercase w-100 p-4 mt-2 above" onClick={this.mapsSelector}>Get Directions</button>
                <button id="Role" className="btn btn-dark font-weight-bold text-uppercase w-100 p-4 mt-4 above" onClick={this.navigate}>Go Back</button>
            </div>
        </div>
       
    </div>
</div>    
    
    );
  } 
}

export default ShowLocation;