import React, { Component } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import Alert from './alert.mp3';
import { navigate } from '@reach/router';
import Firebase from './Firebase';
import Geodist from 'geodist';



const audio = new Audio(Alert);

class Helper extends Component {

  constructor() {
    super();
    this.state = {
      help: true,
      odObject: ""
    };
    
    this.toggleSwitch = this.toggleSwitch.bind(this);
    this.updateAudio = this.updateAudio.bind(this);
    this.silence = this.silence.bind(this)
    this.getData = this.getData.bind(this);
    this.navigate = this.navigate.bind(this);

  }

  getData (lat,lng){
    // Retrieve new posts as they are added to our database
     var ref = Firebase.database().ref('overdose/');
     ref.orderByChild("od").equalTo(1).on("child_added", (snapshot, prev) => {
     var od = snapshot.val();
     var id = snapshot.key;

     var dist = Geodist({lat: od.lat, lon: od.lng}, {lat: lat, lon: lng})
     
     var odObject = {
      id: id,
      lat: od.lat,
      lng: od.lng,
      dist: dist 
    }

    if(dist <= 20){
      this.setState({
        help: false,
        odObject: odObject
      })
      if(this.state.help === false){
        audio.play();
        console.log('running from db');
      }
    }
   }).bind(this);
     }


  toggleSwitch(event){
    event.preventDefault();
    this.setState({
        help: !this.state.help
    })
    if(this.state.help === true){
        audio.play();
        console.log('running from toggle');
    }
  }

 

  updateAudio(event){
    event.preventDefault();
    audio.play();
    // this.setState({
    //    audio: this.state.audio + 1
    // })
    // Put a timer on the audio
    // if(this.state.audio === 6){
    //     audio.pause();
    //     audio.currentTime = 0;
    //     this.setState({
    //         audio: 0
    //      })
    // }
    }

silence(){
    audio.pause();
    audio.currentTime = 0;
    this.setState({
        audio: 0,
        help:true
    });
}

navigate(event){
    event.preventDefault();
    var buttonID = event.target.id;
    if(buttonID === "ShowLocation"){
     navigate(`/${buttonID}/${this.state.odObject.lat}/${this.state.odObject.lng}`);
    } else if(buttonID === "Role"){
     navigate(`/${buttonID}`);
    }
}

  componentDidMount(){

    audio.addEventListener("ended", this.updateAudio);
    this.setState({help:true})

    this.getData(this.props.lat,this.props.lng);

  }

  componentWillUnmount(){
    audio.removeEventListener("ended", this.updateAudio);
    this.setState({
      help:true,
      odObject:null
    })
  }


  render() {
    const { lat, lng } = this.props;
    const {help} = this.state;


    return (
<div className={`p-0 m-0 wrapper ${help === false ? 'bgFlash' : ''}`}>
{help === true ? 
<div className="d-flex  h-100 w-100 p-4 m-0">
    <div className="align-self-center">
    <button onClick={this.toggleSwitch} className="btn btn-secondary">Toggle</button>
    <p>{lat}{"  "}{lng}</p>
        <h4 className="text-center text-uppercase font-weight-bold mb-4">On standby for overdose victims</h4>
        <FontAwesomeIcon icon="heart" size="1x" className="heart text-danger text-center w-100 my-4"/>
        <button id="Role" className="btn btn-dark font-weight-bold text-uppercase w-100 p-4 mt-4" onClick={this.navigate}>Go Back</button>
    </div>
</div>
:
<div className="d-flex h-100 w-100 p-4 m-0">
    <div className="align-self-center">
        <h4 className="text-center text-uppercase font-weight-bold mb-4">A victim is in need of help <span className="text-danger font-weight-bold">{this.state.odObject.dist ? this.state.odObject.dist : 0}</span> miles away!</h4>
        <button id="ShowLocation" className="btn btn-danger font-weight-bold text-uppercase w-100 p-4 mt-2" onClick={this.navigate}>Assist Victim</button>
        <button className="btn btn-dark font-weight-bold text-uppercase w-100 p-2 mt-4" onClick={this.silence}>Silence Alert</button>
    </div>
</div>
}

</div>

    
    );
  } 
}

export default Helper;